import Link from 'next/link';
import styled from 'styled-components';

const ProductList = (props) => {
	const {product} = props;
	const rawJson = product.short_description;
	const productDescription = rawJson.replace(/(<([^>]+)>)/ig,"");
	const HoverPhoto = styled.img`
		:hover {
			background: url('${product.images[1].src}');
		}
	`
	return (
		<div>
			<Link
				href={`/product`}
				as={`/product/${product.slug}`}
			>
				<HoverPhoto>
					
				</HoverPhoto>
			</Link>
			<div className="product-headings">
				<h3>{product.name}</h3>
				<h5>${product.price}</h5>
				<h5 className="product-description">{productDescription}</h5>
			</div>
			<style jsx>{`
				img {
					max-width: 450px;
				}
				.product-headings {
					text-align: center;
				}
				.product-headings {
					text-transform: uppercase;
				}
			`}</style>
		</div>
	);
}

export default ProductList;